.LinkSubmit {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  max-width: 85vw;
  margin-top: 35px;
}

.LinkSubmit .display-container {
  height: 75px;
  min-height: 75px;
  max-width: 85vw;
  margin-bottom: 35px;
}

.LinkSubmit .shortened-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.LinkSubmit button {
  position: relative;
  border: none;
  background: hsla(212, 47%, 57%, 0.7);
  color: white;
  cursor: pointer;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.LinkSubmit button:hover {
  background: hsla(212, 47%, 75%, 0.7);
}

.LinkSubmit .shortened-container button {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-left: 20px;
}

.LinkSubmit .shortened-container button:hover {
  background: hsla(212, 47%, 75%, 0.7);
  padding: 5px; 
  color: #444;
}

.LinkSubmit .short-url {
  font-size: 18px;
  padding: 1em 2em;
  background: rgba(255, 255, 255, 0.5);
  color: rgba(53, 53, 53, 0.7);
  border-radius: 3px;
  display: block;
  margin: 20px 0;
  max-width: 85vw;
}

.LinkSubmit input {
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 1em 2em;
  width: 100%;
  border: none;
  margin-bottom: 25px;
}

.LinkSubmit form button {
  border-radius: 3px;
  font-size: 0.9rem;
  padding: .9em 1.9em;
  width: 100%;
}