*, *:before, *:after {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
}

.App {
  width: 100vw;
  height: 100vh;
  background: #444361;
  background: -webkit-linear-gradient(to bottom, #577288, #444361);
  background: linear-gradient(to bottom, #577288, #444361);
  color: aliceblue;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.App h1 {
  max-width: 85vw;
}

